@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

*::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.4);
  /* border-radius: 10px; */
  background-color: #ffffff;
}

*::-webkit-scrollbar {
  width: 8px;
  background-color: #ffffff;
}

*::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #11101d;
  background-image: -webkit-linear-gradient(90deg,
      transparent,
      rgba(156, 156, 156, 0.4) 50%,
      transparent,
      transparent)
}

body {
  position: relative;
  min-height: 100vh;
  width: 100%;
  /* overflow: hidden; */
}



fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

fieldset+fieldset {
  margin-top: 24px;
}

label {
  margin-bottom: 8px;
  display: block;
  color: #2b2e4a;
}

input:not([type=checkbox]):not([type=radio]),
select,
textarea {
  padding: 8px;
  width: 100%;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid #bdc3c7;
  border-left: 0;
  transition: border-bottom-color 0.15s ease-in;
}

input:not([type=checkbox]):not([type=radio]):focus,
select:focus,
textarea:focus {
  outline: 0;
  border-bottom-color: #3fc1c9;
}

input[type=checkbox],
input[type=radio] {
  margin-right: 8px;
}

textarea {
  resize: vertical;
}

button {
  margin-right: auto;
  margin-left: auto;
  display: block;
  padding: 8px 16px;
  font-size: 16px;
  color: #fff;
  background-color: #11101d;
  border: 0;
  border-radius: 2px;
  cursor: pointer;
  transition: background-color 0.15s ease-in;
}

button:focus:active {
  background-color: #211f39;
  /* background-color: #31a7ae; */
}

button:focus {
  outline: 0;
}

button:hover {
  background-color: #221f39;
}

