.CreateContainer{
    padding: 50px 0;
}
.CreateContainer form div{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.CreateContainer div fieldset {
    margin: 20px 2%;
    width: 21%;
}

.plan{
    display: flex;
}
.plan > * {
    margin: 20px 2%;
    width: 21%!important;
}

.plan button{
    background-color: red;
}
form > .plan button{
    background-color: green;
}