.Search{
    padding: 10px 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end!important
}
.Search > * {
    width: 30%;
    box-sizing: border-box;
}
.Search > div{
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
}
.Search > div > *{
    width: 45%;
}

.search-wrapper{
    border-bottom: none!important;
}
.highlightOption{
    background-color: #3c3c3c;
}

.multiSelectContainer li:hover{
    background-color: #3c3c3c;
}

input:not([type=checkbox]):not([type=radio]):focus,
select:focus,
textarea:focus{
    border-bottom-color: #c5c5c5!important;
}

.toggleSearch{
    margin-left: 15px;
    cursor: pointer;
    font-weight: 900
}
.toggleSearch > svg {
    margin-bottom: -7px;
}

.print{
    padding: 5px 5px 0px 5px;
    font-size: 20px;
    margin: 0;
    margin-right: 10px;
    /* position: relative; */
    /* bottom: -45px; */
    z-index: 5;
    /* left: 65px; */
}