.uploader {
    display: block;
    clear: both;
    margin: 25px auto;
    width: 100%;
    max-width: 600px;
}

.uploader label {
    float: left;
    clear: both;
    width: 100%;
    padding: 2rem 1.5rem;
    text-align: center;
    background: #fff;
    border-radius: 7px;
    border: 3px solid #eee;
    transition: all 0.2s ease;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.uploader label:hover {
    border-color: #11101d;
}

.uploader label.hover {
    border: 3px solid #11101d;
    box-shadow: inset 0 0 0 6px #eee;
}

.uploader label.hover #start i.fa {
    transform: scale(0.8);
    opacity: 0.3;
}

.uploader #start {
    float: left;
    clear: both;
    width: 100%;
}

.uploader #start.hidden {
    display: none;
}

.uploader #start i.fa {
    font-size: 50px;
    margin-bottom: 1rem;
    transition: all 0.2s ease-in-out;
}

.uploader #response {
    float: left;
    clear: both;
    width: 100%;
}

.uploader #response.hidden {
    display: none;
}

.uploader #response #messages {
    margin-bottom: 0.5rem;
}

.uploader #file-image {
    display: inline;
    margin: 0 auto 0.5rem auto;
    width: auto;
    height: auto;
    max-width: 180px;
}

.uploader #file-image.hidden {
    display: none;
}

.uploader #notimage {
    display: block;
    float: left;
    clear: both;
    width: 100%;
}

.uploader #notimage.hidden {
    display: none;
}

.uploader progress,
.uploader .progress {
    display: inline;
    clear: both;
    margin: 0 auto;
    width: 100%;
    max-width: 180px;
    height: 8px;
    border: 0;
    border-radius: 4px;
    background-color: #eee;
    overflow: hidden;
}

.uploader .progress[value]::-webkit-progress-bar {
    border-radius: 4px;
    background-color: #eee;
}

.uploader .progress[value]::-webkit-progress-value {
    background: linear-gradient(to right, #393f90 0%, #11101d 50%);
    border-radius: 4px;
}

.uploader .progress[value]::-moz-progress-bar {
    background: linear-gradient(to right, #393f90 0%, #11101d 50%);
    border-radius: 4px;
}

.uploader input[type=file] {
    display: none;
}

.uploader div {
    margin: 0 0 0.5rem 0;
    color: #5f6982;
}

.uploader .btn {
    display: inline-block;
    margin: 0.5rem 0.5rem 1rem 0.5rem;
    clear: both;
    font-family: inherit;
    font-weight: 700;
    font-size: 14px;
    text-decoration: none;
    text-transform: initial;
    border: none;
    border-radius: 0.2rem;
    outline: none;
    padding: 0 1rem;
    height: 36px;
    line-height: 36px;
    color: #fff;
    transition: all 0.2s ease-in-out;
    box-sizing: border-box;
    background: #11101d;
    border-color: #11101d;
    cursor: pointer;
}

.gallery{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.gallery div{
    width: 300px;
    cursor: pointer;
}
.gallery div img{
    width: 100%;
}

.popUp{
    background: #000000c4;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;
}
.popUp button{
    background-color: red;
    margin-top: 10px;
    z-index: 9999;
}
.popUp button:hover {
    background-color: rgb(222, 0, 0)
}
.rtds-single-slide-styles img{
    height: 75vh!important;
}
.rtds-slider-wrapper{
    padding-top: 45vh!important;
}
.sliderBtn{
    position: absolute;
    top: 45vh;
    font-size: 70px;
    color: white;
    z-index: 78;
    cursor: pointer;
}
.sliderBtn:hover {
    color: rgb(220, 220, 220);
    transform: scale(1.2);
}
.sliderBtn.left {
    left: 10px;
}
.sliderBtn.right {
    right: 10px;
}

.closePopUp{
    position: absolute;
    z-index: 89;
    color: red;
    top: 25px;
    right: 25px;
    cursor: pointer;
    font-weight: 900;
    font-size: 35px;
}
.popUpVideo {
    width: 340px!important;
}
.popUpVideo > button{
    display: none!important;
    background-color: red;
}
.popUpVideo:hover > button{
    display: block !important;
}